<template>
	<nav>

		<!-- Snackbar -->
		<v-snackbar v-model="snackbar" :timeout="4000" top color="success">
			<span>Awesome! You added a new project!</span>
			<v-btn color="grey lighten-2" @click="snackbar = false">Close</v-btn>
		</v-snackbar>	
		<!-- Snackbar -->

		<v-app-bar flat app>
			<v-app-bar-nav-icon class="grey--text" @click = "drawer = !drawer"></v-app-bar-nav-icon>
				<v-toolbar-title class="text-uppercase grey--text">
					<span class="font-weight-light">ToDo</span>
					<span>Vue</span>
				</v-toolbar-title>
					<v-spacer></v-spacer>
				<!-- Dropdown menu -->					
					<v-menu offset-y>
						<template v-slot:activator="{ on }">
             <v-btn v-on="on" class="mr-3" color="grey lighten-4">
            <v-icon left>expand_more</v-icon>
             <span>Menu</span>
            </v-btn>
            </template>
           <v-list>
            <v-list-item v-for="link in links" :key="link.text" router :to="link.route">
             <v-list-item-title>{{ link.text }}</v-list-item-title>
            </v-list-item>
           </v-list>
          </v-menu>
				<!-- Dropdown menu -->					
				<v-btn color="grey lighten-4">
					<span>SignOut</span>
					<v-icon right>exit_to_app</v-icon>
				</v-btn>
		</v-app-bar>
		<v-navigation-drawer app v-model="drawer" color="primary">
			<v-layout column align-center>
				<v-flex class="mt-5">
					<v-avatar size="100">
						<img src="/avatar-1.png">
					</v-avatar>
					<p class="white--text text-center subheading mt-1">#Not_Me</p>
				</v-flex>
				<v-flex class="mt-4 mb-3">
					<Popup @projectAdded="snackbar = true"/>
				</v-flex>
			</v-layout>
				<v-list>
					<v-list-item v-for="link in links" :key="link.text" router :to="link.route">
						<v-list-item-icon>
							<v-icon class="white--text">{{ link.icon }}</v-icon>
						</v-list-item-icon>
						<v-list-item-content>
							<v-list-item-title class="white--text">{{ link.text }}</v-list-item-title>
						</v-list-item-content>
					</v-list-item>
				</v-list>
		</v-navigation-drawer>
	</nav>
</template>

<script>
import Popup from './Popup'

export default {
	components: { Popup },
	data(){
		return {
			drawer: false,
			links: [
				{icon: 'dashboard', text: 'DashBoard', route: '/'},
				{icon: 'folder', text: 'My Projects', route: '/projects'},
				{icon: 'person', text: 'Team', route: '/team'}
			],
			snackbar: false,
		}
	}
}
</script>

