<template>
  <div class="text-center">
    <v-dialog v-model="dialog" width="500">
      <template v-slot:activator="{ on }">
        <v-btn class="success" dark v-on="on">Add New Project</v-btn>
      </template>
      <v-card>
        <v-card-title class="headline grey lighten-2" primary-title>Add a New Project</v-card-title>
      </v-card>
			<v-card-text>
				<v-form class="px-3" ref="form">
					<v-text-field label="Title" v-model="title" prepend-icon="folder" :rules="inputRules"></v-text-field>
					<v-textarea label="Information" v-model="content" prepend-icon="edit" :rules="inputRules"></v-textarea>

					<v-menu v-model="menu">
						<template v-slot:activator="{on}">	
							<v-text-field v-on="on" :value="formatDate" clearable label="Due date" prepend-icon="date_range" :rules="inputRules">
							</v-text-field>
							<v-date-picker v-model="due" :class="{hidden:!menu}"></v-date-picker>
						</template>
          </v-menu>

          <v-spacer></v-spacer>

					<v-btn class="success mx-0 mt-3" @click="submit" :loading="loading">add project</v-btn>
				</v-form>
			</v-card-text>
    </v-dialog>
  </div>
</template>

<script>
import format from 'date-fns/format'
import parseISO from 'date-fns/parseISO'
import db from '@/fb'

export default {
	data() {
    return {
			dialog: false,
			title:'',
			content:'',
			due: null,
			menu: false,
			loading:false,
			inputRules: [
				v => !!v || 'This field is required',
        v => v.length >= 3 || 'Minimum length is 3 characters'
			],
    };
	},
	methods: {
		submit(){
			if(this.$refs.form.validate()){
				this.loading = true
				const project = {
					title: this.title,
					content: this.content,
					due: format(parseISO(this.due),'do MMM yyyy'),
					person: '#Unknown',
					status: 'ongoing'
				}
			db.collection('projects').add(project)
				.then(()=>{
					this.loading = false
					this.dialog = false
					this.$emit('projectAdded')
				})
			}
		},
	},
	computed: {
		formatDate(){
			return this.due ? format(parseISO(this.due),'do MMM yyyy') : '';
		},			
	}
}
</script>

<style>
.hidden {
	visibility: hidden;
}
</style>